
.forecasts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  padding: 0;
  margin: 0 auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.forecasts .item {
    position: relative;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: #fff;
    text-align: center;
    list-style: none;
}
.forecasts .item:first-child:before {
      content: '';
      position: absolute;
      top: -10px;
      left: 50%;
      display: block;
      width: 6px;
      height: 6px;
      margin-left: -3px;
      border-radius: 50%;
      background-color: #fe3f32;
}
.forecasts .item .temp {
      margin-top: -8px;
}



