
body {
  background-color: #fafafa;
}
#app {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1000px;
  height: 600px;
  margin-top: -300px;
  margin-left: -500px;
  border-radius: 3px;
  background-color: #444;
  -webkit-box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
          box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
}
#app:after {
    content: "";
    display: block;
    clear: both;
}
#app #information {
    float: left;
    width: 600px;
}
#app #information #current-weather {
      height: 180px;
      padding-top: 20px;
}
#app #information #forecast {
      height: 400px;
}
#app #map {
    float: left;
    width: 400px;
    height: 600px;
}
hr {
  width: 80%;
  margin: 80px auto;
  border: 0;
  border-top: 1px solid #999;
}



